import {preview} from '../../../common/list.js'
export default{
	name:'banner',
	props:{
		detaildata:Object,
		leaveword:Array
	},
	data(){
		return{
			imgArray:[],
			bannerindex: 0,
			num: 1,
			
		}
	},
	methods:{
		swiperImg(e){
			console.log(e)
			this.bannerindex = e.detail.current
		},
		
		preimg(index){
			preview(index, this.imgArray)
			.then((res) => {
				console.log('预览成功！')
			}).catch((err) => {
				console.log('预览失败！')
			})
		}
	},
	watch:{
		detaildata(newvalue,oldvalue){
			this.imgArray = newvalue.staticimg.slice(0,3)
		}
	}
}
